$sub-color: grey;
$sub-color-light: #efefef;
$main-color: black;

//Estilo para fazer a palavra subir e diminuir de tamanho
@mixin shrinkLabel {
  top: -20px;
  font-size: 12px;
  color: $main-color;
}

.group {
  position: relative;
  margin: 45px 0;

  .form-input {
    background: none;
    background-color: white;
    color: $sub-color;
    font-size: 18px;
    padding: 10px 10px 10px 5px;
    display: block;
    width: 100%;
    border: none;
    border-radius: 0;
    border-bottom: 1px solid $sub-color;
    margin: 25px 0;

    &:focus {
      outline: none;
      background: $sub-color-light;
    }

    &:focus ~ .form-input-label {
      @include shrinkLabel();
    }
  }

  input[type="password"] {
    letter-spacing: 0.3em;
  }

  .form-input-label {
    color: $sub-color;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 5px;
    top: 10px;
    //Configura a transição de qualquer estilo css para ocorrer durante 300ms
    transition: 300ms ease all;
    //Caso haja algo escrito, adiciona a classe "shrink" que faz o texto subir e diminuir de tamanho
    &.shrink {
      //chama a configuração "shrinkLabel" criada no topo do arquivo
      @include shrinkLabel();
    }
  }
}
